import * as React from 'react'
import Accordion from '@mui/material/Accordion'
import AccordionDetails from '@mui/material/AccordionDetails'
import AccordionSummary from '@mui/material/AccordionSummary'
import Box from '@mui/material/Box'
import Container from '@mui/material/Container'
import Typography from '@mui/material/Typography'

import ExpandMoreIcon from '@mui/icons-material/ExpandMore'

export default function FAQ() {
  const [expanded, setExpanded] = React.useState(false)

  const handleChange = (panel) => (event, isExpanded) => {
    setExpanded(isExpanded ? panel : false)
  }

  return (
    <Container
      id='faq'
      sx={{
        pt: { xs: 4, sm: 12 },
        pb: { xs: 8, sm: 16 },
        position: 'relative',
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
        gap: { xs: 3, sm: 6 },
      }}
    >
      <Typography
        component='h2'
        variant='h4'
        color='text.primary'
        sx={{
          width: { sm: '100%', md: '60%' },
          textAlign: { sm: 'left', md: 'center' },
        }}
      >
        Frequently asked questions
      </Typography>
      <Box sx={{ width: '100%' }}>
        <Accordion
          expanded={expanded === 'panel1'}
          onChange={handleChange('panel1')}
        >
          <AccordionSummary
            expandIcon={<ExpandMoreIcon />}
            aria-controls='panel1d-content'
            id='panel1d-header'
          >
            <Typography component='h3' variant='subtitle2'>
              How far in advance should I book my moving services?
            </Typography>
          </AccordionSummary>
          <AccordionDetails>
            <Typography
              variant='body2'
              gutterBottom
              sx={{ maxWidth: { sm: '100%', md: '70%' } }}
            >
              Depending on the time of the year and the size of your move, we
              recommend booking your moving services at least 15 days in
              advance. This will help ensure availability and allow us to
              accommodate your preferred moving date and time.
            </Typography>
          </AccordionDetails>
        </Accordion>
        <Accordion
          expanded={expanded === 'panel2'}
          onChange={handleChange('panel2')}
        >
          <AccordionSummary
            expandIcon={<ExpandMoreIcon />}
            aria-controls='panel2d-content'
            id='panel2d-header'
          >
            <Typography component='h3' variant='subtitle2'>
              What is the payment structure for services rendered?
            </Typography>
          </AccordionSummary>
          <AccordionDetails>
            <Typography
              variant='body2'
              gutterBottom
              sx={{ maxWidth: { sm: '100%', md: '70%' } }}
            >
              We require a 50% deposit to secure your moving date and time. The
              remaining balance is due upon completion of the move. We accept
              cash, credit cards, and certified checks.
            </Typography>
          </AccordionDetails>
        </Accordion>
        <Accordion
          expanded={expanded === 'panel3'}
          onChange={handleChange('panel3')}
        >
          <AccordionSummary
            expandIcon={<ExpandMoreIcon />}
            aria-controls='panel3d-content'
            id='panel3d-header'
          >
            <Typography component='h3' variant='subtitle2'>
              Are GMC services licensed and insured?
            </Typography>
          </AccordionSummary>
          <AccordionDetails>
            <Typography
              variant='body2'
              gutterBottom
              sx={{ maxWidth: { sm: '100%', md: '70%' } }}
            >
              We are fully licensed and insured to provide moving services in
              the state of Florida. Our insurance coverage includes general
              liability, cargo, and workers' compensation. You can rest assured
              that your belongings are protected during the moving process.
            </Typography>
          </AccordionDetails>
        </Accordion>
        <Accordion
          expanded={expanded === 'panel4'}
          onChange={handleChange('panel4')}
        >
          <AccordionSummary
            expandIcon={<ExpandMoreIcon />}
            aria-controls='panel4d-content'
            id='panel4d-header'
          >
            <Typography component='h3' variant='subtitle2'>
              Is there a warranty on the product, and what does it cover?
            </Typography>
          </AccordionSummary>
          <AccordionDetails>
            <Typography
              variant='body2'
              gutterBottom
              sx={{ maxWidth: { sm: '100%', md: '70%' } }}
            >
              Yes, our product comes with a [length of warranty] warranty. It
              covers defects in materials and workmanship. If you encounter any
              issues covered by the warranty, please contact our customer
              support for assistance.
            </Typography>
          </AccordionDetails>
        </Accordion>
      </Box>
    </Container>
  )
}
